import React, { forwardRef } from 'react';
import { isMobile } from 'react-device-detect';
import { Grid, IconButton, makeStyles } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import ListItemText from '@material-ui/core/ListItemText';
import AppBar from '@material-ui/core/AppBar';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import medam from '../assets/ejes/maff.png';
import { Image } from '@material-ui/icons';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import gen from '../assets/ejes/genn.jpg';
import obr from '../assets/ejes/obr.png';
import ecoAm from '../assets/ejes/maff.png';
import noi from '../assets/noimg.jpg';
import colores from '../styles/colors';
import jorOmar from '../assets/ejesProv/jorOmar.jpg'
import begIcon from '../assets/ejesProv/begicon.jpg'


const MyModal = ({ open, handleClose }) => {
	const classes = useStyles();

	return (
		<div>
			<Dialog
				fullScreen
				open={open}
				onClose={handleClose}
				TransitionComponent={Transition}
			
			>
				<AppBar className={classes.appBar}>
					<Toolbar style={{backgroundColor: colores.mainLightfull}}>
						<IconButton
							edge='start'
							color={colores.mainDarkfull}
							onClick={handleClose}
							aria-label='close'
						>
							<CloseIcon />
						</IconButton>
						<Typography variant='h6' className={classes.title}>
							Accion Con Provincia
						</Typography>
					</Toolbar>
				</AppBar>
				<div style={{ padding: isMobile ? 20 : 90, paddingTop: 40, backgroundColor: colores.fondoLight }}>
					<ContentJor/>
				</div>
			</Dialog>
		</div>
	);
};

const ContentJor = () => {
	return (
		<Grid  container spacing={3}   >
			<Grid item xs={12} sm={8} >
			<JorProv />
			</Grid>
            <Grid item xs={12} sm={4} wrap="nowrap">
			<ProvCard />
			<ProvCard />
			<ProvCard />
			<ProvCard />
			</Grid>
			
		</Grid>
	);
};

const JorProv = () => {

	return <Card  style={{  borderTopRightRadius: 15, borderTopLeftRadius: 15 }}>
		<CardMedia style={{ height: 350, borderTopRightRadius: 15, borderTopLeftRadius: 15,backgroundPosition: 'center' }}
					image={jorOmar}
					title={'Jor con Omar'} 

				/>
		<CardContent>
			<Typography variant="h5">
				Recorriendo junto al gobernador
			</Typography>
			<Typography variant="title">
				lorem iptsu set amet log fer lorun ler migon lorem iptsu set amet log fer lorun ler migon
				lorem iptsu set amet log fer lorun ler migonlorem iptsu set amet log fer lorun ler migon
				lorem iptsu set amet log fer lorun ler migonlorem iptsu set amet log fer lorun ler migon
			</Typography>
		</CardContent>
	</Card>
}

const ProvCard = () => {

	return <Card style={{ marginBottom: 30}}>
		
		<Grid container>
		<Grid item xs={6}>
	<CardMedia style={{ height: '100%', width: '100%', minHeight: 150 }}
					image={begIcon}
					title={'Jor con Omar'} />
		</Grid>
		<Grid item xs={6}>
			<CardContent>
				Boleto Educativo Gratuito
			</CardContent>
		</Grid>
		</Grid>
		
		
		
		
	</Card>
}

const Transition = forwardRef(function Transition(props, ref) {
	return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
	appBar: {
		position: 'relative',
	},

	title: {
		marginLeft: 5,
		color: colores.mainDarkfull
	},
}));

export default MyModal;
