import React, { useState } from 'react';
import { isMobile } from 'react-device-detect';
import { makeStyles, Button, Grow, Fade } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import EcoIcon from '@material-ui/icons/Eco';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import fem from '../assets/iconos/fem.png';
import colores from '../styles/colors';
import Tooltip from '@material-ui/core/Tooltip';
import FingerprintIcon from '@material-ui/icons/Fingerprint';
import beg from '../assets/ejesProv/begicon.jpg';

const ejesCamp = [
	{ icon: <FingerprintIcon />, name: 'info', color: colores.ejesBlue },
	{ icon: <EcoIcon />, name: 'ambiente', color: colores.ejesGreen },
	{ icon: <SportsSoccerIcon />, name: 'deportes', color: colores.ejesOrange },
	{
		icon: <Avatar alt='feminismo' src={beg} />,
		name: 'genero',
		color: colores.ejesBlue,
	},
];

const PaperText = () => {
	const styles = useStyles();

	const [ejeType, setEjeType] = useState('info');

	return (
		<>

			<Paper elevation={3} className={styles.paperDesc2}>
				

				<TextEffect active={ejeType} />
			
			
			</Paper>
			<div className={styles.ejesButton}>
				{ejesCamp.map((eje, i) => {
					return (
						<Tooltip
							TransitionComponent={Fade}
							//TransitionProps={{ timeout: 600 }}
							title={eje.name}
						>
							<Grow in={true} timeout={700 * i + 800}>
								<Fab
									style={{ backgroundColor: eje.color }}
									aria-label={eje.name}
									size='medium'
									onClick={() => {
										setEjeType(eje.name);
									}}
								>
									{eje.icon}
								</Fab>
							</Grow>
						</Tooltip>
					);
				})}
			</div>
		</>
	);
};

const TextEffect = ({ active }) => {
	return (
		<div>
			{Object.keys(texts).map((key) => {
				if (key === active) {
					return (
						<Grow in={true} timeout={500}>
							<Typography >
								{texts[key]}
								{key === 'genero' && (
									<Button
										style={{ margin: 10 }}
										variant='contained'
										color='primary'
									>
										Inscribirse !
									</Button>
								)}
							</Typography>
						</Grow>
					);
				}
			})}
		</div>
	);
};

const texts = {
	info: 'Soy Jorgelina, nací en Santa Fe. Soy Vicepresidenta Segunda del	Concejo Deliberante de Nuestra Ciudad. Mi compromiso es trabajar todos	los días para contribuir a que seamos una ciudad pujante, inclusiva y con igualdad de oportunidades para todos los santafesinos y santafesinas',
	deportes:
		'opcion 1: botones con breve desc de cada uno de sus ejes, en el boton de abajo iria la informacion expandida',
	genero:
		'opcion 2: usar estos iconos/botones para accion con prov, reemplazarlos por BEG, BOLETO, VACUNACION, etc, con breve texto y boton para ir a la web',
	ambiente:
		' ver boton siguiente',
};

const useStyles = makeStyles((theme) => ({
	paperDesc2: {
		minHeight: isMobile ? 180 : 220,
		maxHeight: isMobile ? 180 : 350,
		overflow: 'auto',
		padding: isMobile ? 18 : 35,
	
		borderRadius: 10,
		backgroundColor: 'rgba(228, 235, 241,0.7)',
		textAlign: isMobile ? 'center' : 'justify',
		
	},
	ejesButton: {
		marginTop: isMobile ? -18 : -25,
		width: isMobile ? '75%' : '50%',
		marginRight: 'auto',
		marginLeft: 'auto',
		display: 'flex',
		justifyContent: 'space-between',
	},
	iconSize: { height: 28, width: 28 },
}));

export default PaperText;
