import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { isMobile } from 'react-device-detect';
import SpeedDial from '@material-ui/lab/SpeedDial';
import SpeedDialIcon from '@material-ui/lab/SpeedDialIcon';
import SpeedDialAction from '@material-ui/lab/SpeedDialAction';
import YouTubeIcon from '@material-ui/icons/YouTube';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import FavoriteIcon from '@material-ui/icons/Favorite';
import TwitterIcon from '@material-ui/icons/Twitter';

const useStyles = makeStyles((theme) => ({
	root: {
		height: 150,
		transform: 'translateZ(0px)',
		flexGrow: 1,
	},
	speedDial: {
		position: 'absolute',
		top: isMobile ? theme.spacing(2) : theme.spacing(28),
		left: isMobile ? theme.spacing(1) : theme.spacing(3),
	},
}));

const actions = [
	{ icon: <InstagramIcon />, name: 'Seguime en Instagram' },
	{ icon: <YouTubeIcon />, name: 'Subscribite al canal' },
	{ icon: <FacebookIcon />, name: 'Seguime en Facebook' },
	{ icon: <TwitterIcon />, name: 'Seguime en Twitter' },
];

export default function OpenIconSpeedDial() {
	const classes = useStyles();
	const [open, setOpen] = React.useState(isMobile ? false : true);

	const handleOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};

	return (
		<div className={classes.root}>
			<SpeedDial
				ariaLabel='SpeedDial openIcon example'
				className={classes.speedDial}
				direction='down'
				icon={<SpeedDialIcon color='secondary' openIcon={<FavoriteIcon />} />}
				onClose={isMobile ? handleClose : handleClose}
				onOpen={handleOpen}
				open={open}
			>
				{actions.map((action) => (
					<SpeedDialAction
						FabProps={{ size: isMobile ? 'small' : 'large' }}
						key={action.name}
						icon={action.icon}
						tooltipTitle={action.name}
						tooltipPlacement='left'
						//tooltipOpen
						onClick={handleClose}
					/>
				))}
			</SpeedDial>
		</div>
	);
}
