import { useState } from 'react';
import { IconButton, makeStyles, Typography, withStyles } from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import BookmarksIcon from '@material-ui/icons/Bookmarks';
import ShareIcon from '@material-ui/icons/Share';
import EjesModal from './ejesModal';
import DashboardIcon from '@material-ui/icons/Dashboard';
import SimpleMap from './map';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';

import colores from '../styles/colors';
import Fab from '@material-ui/core/Fab';
import Paper from '@material-ui/core/Paper';
import ProyectosModal from './proyectModal'
import MyModal from './myModal'

import { Slide } from '@material-ui/core';

const NavigationBar = (props) => {
	const classes = useStyles();
	const [value, setValue] = useState(null);
	const [ejesHandler, setEjesHandler] = useState(false);
	const [proyectHandler, setProyectHandler] = useState(false);
	const [myHandler, setMyHandler] = useState(false);

	const mainButtons = [
		{
			icon: <ShareIcon />,
			name: 'accion con Provincia',
			func: () => setMyHandler(true),
		},
		{
			icon: <DashboardIcon />,
			name: 'ejes de campaña',
			func: () => setEjesHandler(true),
		},
		{
			icon: <BookmarksIcon />,
			name: 'proyectos HCSF',
			func: () => setProyectHandler(true),
		},
	];

	return (<>
		<div
			className={classes.bottomNav}
		>
			<BottomNavigation
				value={value}
				onChange={(event, newValue) => {
					setValue(newValue);
				}}
				className={classes.paperMain}
			>
				{mainButtons.map((btn, i) => (
					<BottomNavigationAction
						icon={
							 <Slide direction="up" in={true} mountOnEnter  timeout={400 * i + 900}>
							
								<Fab aria-label={btn.name} key={btn.name} className={classes.FabBtn}   >
									{btn.icon}
								</Fab>
								
							</Slide>
							
						}
						showLabel
						onClick={btn.func}
						label={
							<Paper
								className={classes.tipBtn}
							>
								<Typography style={{fontSize: 14}} > {btn.name}</Typography>
							</Paper>
						}
						value={i}
					/>
				))}
			</BottomNavigation>
		</div>

		<EjesModal
				open={ejesHandler}
				handleClose={() => {
					setEjesHandler(false);
					setValue(null);
				}}
			/>

			<ProyectosModal open={proyectHandler} handleClose={() => {
					setProyectHandler(false);
					setValue(null);
				}}
			/>

			<MyModal open={myHandler} handleClose={() => {
					setMyHandler(false);
					setValue(null);
				}}
			/>
	</>);
};

const useStyles = makeStyles(() => ({
	circularBtn: {
		borderWidth: 2,
		borderColor: 'blue',
	},
	paperMain: {
		marginRight: 'auto',
		marginLeft: 'auto',

		backgroundColor: colores.mainDark,
		width: isMobile ? 290 : 420,
		borderRadius: 15,
	},
	bottomNav: {
				position: 'absolute',
				bottom: 40,
				alignSelf: 'center',
			},
	tipBtn: {
		marginTop: 5,
		padding: 2,
		backgroundColor: colores.mainLight
	
	},
	fabBtn: {backgroundColor: colores.mainLight}
}));

export default NavigationBar;
