import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import jorP from '../assets/iconos/jorP.jpeg';
import concejoLogo from '../assets/iconos/concejo2.png';
import Avatar from '@material-ui/core/Avatar';
import Divider from '@material-ui/core/Divider';

import {
	Card,
	CardContent,
	CardHeader,
	CardMedia,
	Paper,
	Typography,
} from '@material-ui/core';

const AnyReactComponent = () => (
	<div>
		<Avatar
			style={{
				height: 60,
				width: 60,
				marginBottom: 4,
			}}
			src={jorP}
			alt='marker'
		/>
		<Card
			style={{
				width: 200,

				marginLeft: -70,
				textAlign: 'center',
				fontSize: 22,
				letterSpacing: 2,

				borderRadius: 7,
			}}
			elevation={4}
		>
			<CardMedia
				style={{ height: 70, width: '100', alignSelf: 'center' }}
				image={concejoLogo}
				title='Contemplative Reptile'
			/>
			<Divider variant='middle' />
			<div style={{ padding: 10 }}>
				<Typography style={{ letterSpacing: 1.4 }} variant='subtitle2'>
					Honorable Concejo De Santa Fe
				</Typography>

				<Typography style={{ letterSpacing: 1.4 }} variant='body1'>
					Salta 2951
				</Typography>
			</div>
		</Card>
	</div>
);

class SimpleMap extends Component {
	static defaultProps = {
		center: {
			lat: -31.64978408078189,
			lng: -60.71202220247979,
		},
		zoom: 16,
	};

	render() {
		return (
			// Important! Always set the container height explicitly
			<div style={{ height: '100%', width: '100%' }}>
				<GoogleMapReact
					bootstrapURLKeys={{
						key: process.env.REACT_APP_GOOGLE_MAPKEY,
						language: 'es',
					}}
					defaultCenter={this.props.center}
					defaultZoom={this.props.zoom}
					options={{
						scrollwheel: false,
						disableDefaultUI: true,
					}}
				>
					<AnyReactComponent
						lat={-31.64864408078189}
						lng={-60.71272220247979}
					/>
				</GoogleMapReact>
			</div>
		);
	}
}

export default SimpleMap;
